import React, {useState, useEffect} from 'react';
import './App.css';
import { Button, FormControl, InputLabel, Input, Icon } from '@material-ui/core';
import Message from './Message';
import './Message.css';
import db from './firebase';
import firebase from 'firebase';
import FlipMove from 'react-flip-move';
import SendIcon from '@material-ui/icons/Send';
import { IconButton } from '@material-ui/core';



function App() {
  const [input, setInput] = useState('')
  const [messages, setMessages] = useState([]);
  const [username, setUsername] = useState('');
  
  // useState = variable in REACT
  // useEffect = run code on a condition in REACT
  
  useEffect( () => {
    db.collection('messages')
    .orderBy('timestamp', 'desc')
    .onSnapshot(snapshot => {
      setMessages(snapshot.docs.map(doc => ({id: doc.id, message: doc.data()})))
    });
  }, [] )


  useEffect(() => {
   setUsername(prompt('please enter your name'));
  }, [] ) // condtion
   
  const sendMessage = (event) =>{
    event.preventDefault();

    db.collection('messages').add({
      message: input,
      username: username,
      timestamp: firebase.firestore.FieldValue.serverTimestamp()
    });
    setInput('');

  }


  return (
    <div className="App">
      <img src="http://store-images.s-microsoft.com/image/apps.7488.13510798886918977.69182166-f125-495d-80d2-44fdaab21523.8fcea13e-5d9a-48a9-9937-b26deeced1b5" alt=""/>
        <h1>Hello Friends</h1>
        <h2>Welcome  {username}</h2>
      
       <form className="app_form">

       <FormControl className="app_formControl">
       <Input className="app_input" placeholder='Enter a message' value={input} onChange={event => setInput(event.target.value)}/>
       <IconButton className="app_iconButton" disabled={!input} variant="contained" color='primary' type='submit' onClick={sendMessage}>
        <SendIcon/>
       </IconButton>
       </FormControl>
       
      </form>

      <FlipMove>
      {
       messages.map(({id, message}) => (
         <Message key={id} username={username} message={message} />
       ))
     }
      </FlipMove>


    </div>
  );
}

export default App;
